.list-todo-container {
  button {
    cursor: pointer;
  }

  .add-todo {
    .add {
      margin: 0 10px;
    }
  }

  .list-todo-content {
    width: 400px;
    margin: 0 auto;

    .todo-child {
      &:first-child {
        border-top: 1px solid grey;
      }

      height: 40px;
      border-bottom: 1px solid grey;
      margin: 10px;

      .edit {
        margin: 0 10px;
      }
    }
  }
}
